









import Vue from 'vue';
import { formatDate } from '@/helper';

export default Vue.extend({
  name: "RecentlyCompletedWorkout",
  props: {
    // The name of the workout.
    workoutName: { 
      type: String,
      required: true,
      default: ""
    },
    // The date the workout was completed.
    completedDate: {
      type: String,
      required: true
    }
  },
  methods: {
    /**
     * The format date method from helper.js.
     */
    formatDate
  }
})
